'use client'

import React from "react";
import {
  Modal,
  ModalHeader,
  ModalBody,
  ModalFooter,
  Button,
} from "reactstrap";
import "react-datepicker/dist/react-datepicker.css";
import { useRouter } from "next/navigation";
import { useTranslation } from "react-i18next";
import { useStateMachine } from "little-state-machine";
import {
  updateBooking,
  updateAction,
  updateUser,
} from "../../utils/updateActions";
import { SpinnerCircular } from 'spinners-react';

interface SummaryModalProps {
  createBooking: () => void;
  working: boolean;
  modalSummary: { modal_static: boolean };
  setWorking: (value: boolean) => void;
  setModalSummary: (value: { modal_static: boolean }) => void;
  buttonText?: string;
}


const SummaryModal: React.FC<SummaryModalProps> = ({
  createBooking,
  working,
  modalSummary,
  setWorking,
  setModalSummary,
  buttonText = "Pay Now",
}) => {
  const { t } = useTranslation();
  const { state } = useStateMachine({
    updateBooking,
    updateAction,
    updateUser,
  });

  const trip = (state?.trip as any)?.trip;
  const bookingFee = trip?.price ? (trip.price / 100 * 10 > 500 ? 500 : trip.price / 100 * 10) : 0;
  const totalToPay = trip?.price ? (trip.price / 100 * 10 > 500 ? trip.price + 500 : trip.price / 100 * 10 + trip.price) : 0;

  return (
    <>
      <Modal isOpen={modalSummary.modal_static} backdrop="static" centered size="md">
        <ModalHeader toggle={() => setModalSummary({ modal_static: false })}>
          <h3>{t('ticket_summary')}</h3>
        </ModalHeader>
        <ModalBody>
          <div className="row">
            <div className="col-md-9 col-lg-6 col-xl-5 mx-auto">
              <ul className="">
                <li>{t('Agency')}: <span>{trip?.agency_name || trip?.agencyName}</span></li>
                <li>{t('from')}: <span>{trip?.from_location || trip?.from}</span></li>
                <li>{t('to')}: <span>{trip?.to_location || trip?.to}</span></li>
                <li>{t('journey_date')}: <span>{trip?.journey_date || trip?.journeyDate}</span></li>
                <li>{t('time')}: <span>{trip?.departure || trip?.departure_time}</span></li>
                <li>{t('fare')}: <span>{trip?.price}</span></li>
                <li>{t('booking_fee')}: <span>{bookingFee}</span></li>
                <li>{t('to_pay')}: <span>{totalToPay}</span></li>
              </ul>
            </div>
          </div>

          <ModalFooter>
            <div className="text-center">
              <p className="summary-p">Note: {t('reservation_note')}</p>
            </div>
            {working ? (
              <div style={{ display: 'flex', justifyContent: 'center', alignItems: 'center', padding: '20px' }}>
                <SpinnerCircular
                  color="#bcc015"
                  size={50}
                  aria-label="Loading Spinner"
                  data-testid="loader"
                />
              </div>
            ) : (
              <>
                <Button
                  color="light"
                  onClick={() => setModalSummary({ modal_static: false })}
                >
                  {t("cancel")}
                </Button>
                <Button type="submit" onClick={createBooking} color="primary">
                  {buttonText}
                </Button>
              </>
            )}
          </ModalFooter>
        </ModalBody>
      </Modal>
    </>
  );
};

export default SummaryModal;
