'use client'

import React, { useEffect } from "react";
import {
  Row,
  Col,
  Modal,
  ModalHeader,
  ModalBody,
  ModalFooter,
  Button,
} from "reactstrap";
import "react-datepicker/dist/react-datepicker.css";
import { useTranslation } from "react-i18next";
import { useStateMachine } from "little-state-machine";
import {
  updateBooking,
  updateAction,
  updateUser,
} from "../../utils/updateActions";
import { useForm } from "react-hook-form";
import { SpinnerCircular } from 'spinners-react';
import type { Trip } from "../../interfaces/trips.interface";

interface ReservationModalProps {
  modal: { modal_static: boolean };
  getModal: (value: { modal_static: boolean }) => void;
  booking?: Trip;
  showSummary: () => void;
  isLoading: boolean;
  setIsLoading: (value: boolean) => void;
  buttonText?: string;
}

interface BookingFormData {
  name: string;
  idCardNo: string;
  phoneNumber: string;
  payerEmail: string;
}


const ReservationModal: React.FC<ReservationModalProps> = ({
  modal,
  getModal,
  booking,
  showSummary,
  isLoading,
  setIsLoading,
  buttonText = "Continue",
}) => {
  const { t } = useTranslation();
  const {
    register,
    reset,
    getValues,
    handleSubmit,
    formState: { errors },
  } = useForm<BookingFormData>();

  const { state } = useStateMachine({
    updateBooking,
    updateAction,
    updateUser,
  });

  const saveLocal = async (): Promise<void> => {
    setIsLoading(true);
    const phone = getValues("phoneNumber");
    const name = getValues('name');
    const idCard = getValues('idCardNo');
    const payerEmail = getValues('payerEmail');

    if (typeof window !== 'undefined') {
      localStorage.setItem("phoneNumber", phone);
      localStorage.setItem("name", name);
      localStorage.setItem("idCard", idCard);
      localStorage.setItem("email", payerEmail);
    }

    getModal({ modal_static: false });
    showSummary();
  };

  useEffect(() => {
    if (booking) {
      reset({
        data: booking,
      } as any);
    }
  }, [booking, reset]);

  const trip = (state?.trip as any)?.trip;

  return (
    <>
      <Modal isOpen={modal.modal_static} backdrop="static" centered size="lg">
        <ModalHeader toggle={() => getModal({ modal_static: false })}>
          Destination: {trip?.to_location || trip?.to}
        </ModalHeader>
        <ModalBody>
          <form onSubmit={handleSubmit(saveLocal)}>
            <Row>
              <Col lg={6}>
                <div className="form-group">
                  <label htmlFor="name">{t("name")}</label>
                  <input
                    type="text"
                    name="name"
                    id="name"
                    className="form-control"
                    {...register("name", {
                      required: true,
                      minLength: 6,
                    })}
                  />
                </div>
                {errors.name && (
                  <p style={{ color: "red" }}>{t("agency_branch_err")}</p>
                )}
              </Col>
              <Col lg={6}>
                <div className="form-group">
                  <label htmlFor="idCardNo">{t("id_card")}</label>
                  <input
                    type="text"
                    name="idCardNo"
                    id="idCardNo"
                    className="form-control"
                    {...register("idCardNo", {
                      required: true,
                    })}
                  />
                </div>
                {errors.idCardNo && (
                  <p style={{ color: "red" }}>{t("id_card_err")}</p>
                )}
              </Col>

              <Col lg={6}>
                <div className="form-group">
                  <label htmlFor="phoneNumber">{t("phone")}</label>
                  <input
                    type="number"
                    name="phoneNumber"
                    id="phoneNumber"
                    className="form-control"
                    {...register("phoneNumber", {
                      required: true,
                      minLength: 9,
                      maxLength: 12,
                    })}
                  />
                </div>
                {errors.phoneNumber && (
                  <p style={{ color: "red" }}>{t("phone_err")}</p>
                )}
              </Col>

              <Col lg={6}>
                <div className="form-group">
                  <label htmlFor="payerEmail">{t("email_address")}</label>
                  <input
                    type="email"
                    name="payerEmail"
                    id="payerEmail"
                    className="form-control"
                    {...register("payerEmail", {
                      required: true,
                    })}
                  />
                </div>
                {errors.payerEmail && (
                  <p style={{ color: "red" }}>{t("phone_err")}</p>
                )}
              </Col>
            </Row>

            <ModalFooter>
              {isLoading ? (
                <div style={{ display: 'flex', justifyContent: 'center', alignItems: 'center', padding: '20px' }}>
                  <SpinnerCircular
                    color="#bcc015"
                    size={50}
                    aria-label="Loading Spinner"
                    data-testid="loader"
                  />
                </div>
              ) : (
                <>
                  <Button
                    color="light"
                    onClick={() => getModal({ modal_static: false })}
                  >
                    {t("cancel")}
                  </Button>
                  <Button type="submit" color="primary">
                    {buttonText}
                  </Button>
                </>
              )}
            </ModalFooter>
          </form>
        </ModalBody>
      </Modal>
    </>
  );
};

export default ReservationModal;
